export default {
	data() {
		return {
			title: 'Hello',
			oldPwd: '',
			newPwd: '',
			newPwd2: ''
		}
	},
	onLoad() {

	},
	methods: {
		clearInput(val) { //清除密码
			if (val == 'oldPwd') {
				this.oldPwd = '';
				console.log('清除旧密码')
			}
			if (val == 'newPwd') {
				this.newPwd = '';
				console.log('清除新密码')
			}
			if (val == 'newPwd2') {
				this.newPwd2 = '';
				console.log('清除新密码2')
			}
		},
		updPwdByPwd() { // 修改密码
			// 对比两次密码
			if (this.newPwd != this.newPwd2) {
				uni.showToast({
					title: '两次密码不一致',
					duration: 2000,
					icon:'none'
				});
				return ;
			}
			// 打开动画
			uni.showLoading({
				title: '提交中...',
				mask: true
			});
			uni.request({
				url: this.$serverUrl + '/api/member/updPwdByPwd',
				method: 'POST',
				header:{
					'token':uni.getStorageSync('userinfo').token
				},
				data: {
					oldPassword:this.oldPwd,
					newPassword:this.newPwd,
					member_id:uni.getStorageSync('userinfo').member_id
				},
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log('请求失败', ret);
						return;
					}
					if (ret.data.code == 0) {
						// 关闭动画
						uni.hideLoading();
						uni.showToast({
							title: ret.data.message,
							duration: 2000
						});
					} 
					if (ret.data.code == 1) {
						// 关闭动画
						uni.hideLoading();
						uni.showToast({
							title: ret.data.message,
							duration: 2000,
							icon:'none'
						});
					}
					if (ret.data.code == 2) {
						// 气泡提示
						uni.showToast({
							title: '数据异常，请重新登录',
							duration: 2000,
							success() {
								uni.reLaunch({
									url: '../login/login'
								});
							}
						});
					} 
				}
			});
		}
	}
}
